/* src/styles/HomePage.css */
.content {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1px; /* Espacio entre cada secci坦n de imagen */
    
}

.image-row {
    display: flex;
    justify-content: center; /* Centra las im叩genes dentro de la fila */
    gap: 0px; /* Espacio entre las dos im叩genes */
    width: 100%;
    max-width: 2000px; /* Limita el ancho m叩ximo de la fila */
}

.image-row .image-section {
    flex: 1; /* Hace que las im叩genes ocupen el mismo espacio */
    max-width: 800px; /* Limita el ancho m叩ximo de cada imagen */
}

.image-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0; /* Espacio entre cada secci坦n */
    width: 100%;
}

.image {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 10px; /* Bordes redondeados, opcional */
}
