

/* Fondo animado en background-layer */

@keyframes sinkEffect {
    0% {
        transform: scale(1.2); /* Comienza ligeramente m叩s grande */
        opacity: 1;
    }
    50% {
        transform: scale(1.2); /* Mantiene el tama単o grande durante la pausa */
        opacity: 1;
    }
    100% {
        transform: scale(1); /* Termina en el tama単o normal */
        opacity: 1;
    }
}
.background-layer {
    background: url('../assets/background.jpg') no-repeat center center/cover;
    background-size: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1; /* Coloca el fondo detr叩s del contenido */
    animation: sinkEffect 1.7s ease-in-out;
    animation-fill-mode: forwards;
}

/* Mant辿n el estilo de welcome-section sin la animaci坦n */
.welcome-section {
    position: relative;
    height: 100vh;
    width: 96vw;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    color: #fff;
    padding: 20px;
}

.welcome-text {
    align-self: flex-start;
    max-width: 60%;
    margin: auto 0;
    text-align: left;
    padding-left: 15em;
    height: 20vh;
}

@keyframes slideInUp {
    0% {
        transform: translateY(50%); /* Empieza fuera de la pantalla por abajo */
        opacity: 0; /* Comienza invisible */
    }
    100% {
        transform: translateY(0); /* Llega a su posici坦n final */
        opacity: 1; /* Termina completamente visible */
    }
}

.welcome-text h1 {
    font-size: 5rem;
    font-weight: 500;
    margin-bottom: 0.05rem;
    opacity: 0; /* Oculta inicialmente */
    animation: slideInUp 1.5s ease-in-out 1s forwards; /* Aplica deslizamiento y mantiene el estado final */
}

.welcome-text p:nth-of-type(1),
.welcome-text p:nth-of-type(2) {
    color: #fff;
    font-size: 2rem;
    margin-bottom: 0.2rem;
    opacity: 0; /* Oculta inicialmente */
    animation: slideInUp 1.5s ease-in-out 1s forwards; /* Retardo para aparecer despu辿s del t鱈tulo */
}



@keyframes fadeIn {
    0% {
        opacity: 0; /* Empieza invisible */
    }
    100% {
        opacity: 1; /* Termina completamente visible */
    }
}

.cta-button {
    align-self: center;
    background-color: rgba(0, 0, 0, 0.4);
    color: white;
    width: 10em;
    padding: 0.5rem 0;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
    transition: background-color 0.3s ease;
    margin-bottom: 50px;
    opacity: 0; /* Oculta inicialmente */
    animation: fadeIn 1s ease-in-out 0.5s forwards; /* Se desvanece y mantiene su estado visible */
}



.cta-button:hover {
    background-color: rgba(0, 0, 0, 0.7);
}








